import {useContext,Fragment} from 'react';
import {SurveyContext, ThemeContext} from '../../utils/contexte';
import { useFetch } from '../../utils/hooks';
import styled from 'styled-components';
import colors from '../../utils/styles/colors';
import {Link} from 'react-router-dom';
import {Loader} from '../../utils/styles/Atoms'

const ResultContainer = styled.div`
    width:75%;
    display: flex;
    flex-direction:column;
    justify-content:center;
    align-items:center;
    margin:0px 5%;
    padding: 50px;
    color:${ ({theme})=> (theme === 'light') ? '#000' : '#fff'};
    background: ${ ({theme})=> (theme === 'light') ? colors.backgroundLight : colors.backgroundDark}
`

const TitleWrapper = styled.div`
    width:450px;
    font-size:30px;
    color: ${ ({theme})=> (theme === 'light') ? '#000' : '#fff'};
    text-align:center;
`

const StyledTitle = styled.span`
    color: ${colors.primary};
`
const StyledTitle2 = styled.span`
    color: ${colors.primary};
    line-height:30px;
    font-size:20px;
`

const DescWrapper = styled.div`
    width:75%;
`
const StyledDesc = styled.div`
    color: ${ ({theme})=> (theme === 'light') ? colors.secondary : '#fff'};
    line-height:30px;
`

const StyledGroup = styled.div`
    margin:30px;
`
const StyledLink = styled(Link)`
    padding: 15px;
    color: #8186a0;
    text-decoration: none;
    font-size: 18px;
    ${(props) => props.$isFullLink &&
        `color: white; border-radius: 30px; background-color: ${colors.primary};
         padding: 10px 80px;`}
`

const LinkWrapper = styled.div`
    margin:70px;
`

export function formatQueryParams(answers){
    const answerNumbers = Object.keys(answers);

    return answerNumbers.reduce((previousParam,answerNumber,index)=>{
        const isFirstAnswer = index === 0;
        const separator = isFirstAnswer ? '' : '&' ;

        return `${previousParam}${separator}a${answerNumber}=${answers[answerNumber] ? '1' : '0' }`

    },'')
}

export function formatJobList(title,listLength,index){
    if(index === listLength -1){
        return title;
    }
    return `${title},`;
}

function Results(){
    const {answers} = useContext(SurveyContext);
    const {theme} = useContext(ThemeContext);
    const queryParams = formatQueryParams(answers);
    const {data,isLoading,error} = useFetch(`http://localhost:8000/results/?${queryParams}`)
    const {resultsData} = data;        
    
    if (error) {
        return <span>Oups il y a eu un problème</span>
    }

    return(
        <ResultContainer theme={theme}>
            {isLoading ? 
            (<Loader/> ): 
            (
                (resultsData.length <= 0) ? 
                (<div></div>) :
                (<Fragment> <TitleWrapper theme={theme}>Les competences dont vous avez besoins sont : {
                                resultsData && resultsData.map((result,index)=>
                                {
                                    return  <StyledTitle theme={theme} key={result.title}>
                                                {formatJobList(result.title,resultsData.length,index)}
                                            </StyledTitle>
                                })}
                            </TitleWrapper>

                        <LinkWrapper theme={theme} >
                            <StyledLink to="/freelances" $isFullLink >Faire le test</StyledLink>
                        </LinkWrapper>

                        <DescWrapper theme={theme}>{ 
                            resultsData && resultsData.map((result,index)=>
                            {
                                return (<StyledGroup theme={theme} key={`${index}cle`}>
                                    <StyledTitle2 theme={theme}>{result.title}</StyledTitle2>
                                    <StyledDesc theme={theme}>{result.description}</StyledDesc>
                                </StyledGroup>)
                            })}
                        </DescWrapper>
            </Fragment>))}
        </ResultContainer>
    )
}
export default Results;