import { useParams, Link } from "react-router-dom";
import {useContext} from 'react';
import styled from "styled-components";
import colors from '../../utils/styles/colors';
import {Loader} from '../../utils/styles/Atoms.jsx';
import { SurveyContext } from "../../utils/contexte";
import { ThemeContext } from "../../utils/contexte";
import { useFetch } from "../../utils/hooks";

const SurveyContainer = styled.div`
    display: flex;
    flex-direction:column;
    align-items: center;
    justify-content:center;
    color:${({theme})=>(theme === 'light') ? '#000' : '#fff'};
    gap:100px;
    
`

const LinkWrapper = styled.div`
    display: flex;
    gap:50px;
    font-size: 18px;
`

const QuestionTitle = styled.div`
    font-size : 25px;
    font-weight:bold;
    text-decoration: underline ${colors.primary};

`

const QuestionContent = styled.div`
    font-size : 20px;
    width:70%;
    text-align: center;
    line-height:50px;
`

const StyledLink = styled(Link)`
    padding: 15px;
    text-align: center;
    color: #8186a0;
    text-decoration: none;
    font-size: 18px;
    color:#fff; 
    border-radius: 30px; 
    background-color: ${colors.primary};
    padding: 10px 10px;
    width: 150px;
`

const LoaderWrapper = styled.div`
  display: flex;
  justify-content: center;
`

const ReplyBox = styled.button`
  border: none;
  height: 100px;
  width: 300px;
  color:${({theme})=>(theme === 'light') ? '#000' : '#fff'};
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: ${({theme})=>(theme === 'light') ? colors.backgroundLight : colors.backgroundDark};
  border-radius: 30px;
  cursor: pointer;
  box-shadow: ${(props) =>
    props.isSelected ? `0px 0px 0px 2px ${colors.primary} inset` : 'none'};
  &:first-child {
    margin-right: 15px;
  }
  &:last-of-type {
    margin-left: 15px;
  }
`
const ReplyWrapper = styled.div`
  display: flex;
  flex-direction: row;
`


function Survey(){

    const {questionNumber} = useParams();
    const suiv =  parseInt(questionNumber)===6 ? '/results' : '/survey/'+(parseInt(questionNumber)+1);
    const prec = parseInt(questionNumber)===1 ? '/survey/1' : '/survey/'+(parseInt(questionNumber)-1);
    const { answers, saveAnswers } = useContext(SurveyContext);
    const {theme} = useContext(ThemeContext);

    const {data,isLoading,error} = useFetch('http://localhost:8000/survey');
    const {surveyData} = data; 

    const saveReply=(answer)=>{
        saveAnswers({[questionNumber]: answer});
    }

    if (error) {
        return <span>Oups il y a eu un problème</span>
    }

    return (
    <SurveyContainer theme={theme}>
        <QuestionTitle>Question {questionNumber}</QuestionTitle>
        {
            isLoading ? 
            (<LoaderWrapper><Loader /></LoaderWrapper>) :
            (<QuestionContent>{surveyData && surveyData[questionNumber]}</QuestionContent>)
        }

        <ReplyWrapper>
            <ReplyBox theme={theme} onClick={()=>saveReply(true)} isSelected={answers[questionNumber] === true} >
                Oui
            </ReplyBox>
            <ReplyBox theme={theme}  onClick={()=>saveReply(false)} isSelected={answers[questionNumber] === false} >
                Non
            </ReplyBox>
        </ReplyWrapper>
        <LinkWrapper>
            <StyledLink to={prec}>Precedent</StyledLink>
            <StyledLink to={suiv}>Suivant</StyledLink>
        </LinkWrapper>
    </SurveyContainer>)
}
export default Survey;