import styled from 'styled-components';
import { Link } from 'react-router-dom';
import colors from '../../utils/styles/colors';
import homeIllustration from '../../assets/home-illustration.svg';
import { ThemeContext } from '../../utils/contexte';
import { useContext } from 'react';

const HomeContainer = styled.div`
    width:75%;
    display: flex;
    margin:30px 5%;
    padding: 100px;
    background: ${ ({theme})=> (theme === 'light') ? colors.backgroundLight : colors.backgroundDark}
`

const StyledLink = styled(Link)`
    padding: 15px;
    color: #8186a0;
    text-decoration: none;
    font-size: 18px;
    ${(props) => props.$isFullLink &&
        `color: white; border-radius: 30px; background-color: ${colors.primary};
         padding: 10px 80px;`}
`

const TextSecond = styled.h1`
    font-size:50px;
    color:${({theme})=>(theme === 'light') ? '#000' : '#fff'}
`

function Home() {
    const {theme} = useContext(ThemeContext);
    return (
            <HomeContainer theme={theme} >
                <div>
                    <TextSecond theme={theme}>Repérez vos besoins,on s’occupe du reste, avec les meilleurs talents</TextSecond>
                    <div style={{marginTop:130}} ><StyledLink to="/survey/1" $isFullLink >Faire le test</StyledLink></div>
                </div>
                <div>
                    <img src={homeIllustration} alt="Home-illustration " />
                </div>
            </HomeContainer>)
}

export default Home;
