import Card from '../../components/Card';
import styled from 'styled-components';
import colors from '../../utils/styles/colors';
import  {useContext} from 'react';
import { Loader } from '../../utils/styles/Atoms';
import { ThemeContext } from '../../utils/contexte' ;
import { useFetch } from "../../utils/hooks";


const CardContainer = styled.div`
    display: grid;
    gap:24px;
    grid-template-rows: 350px 350px;
    grid-template-columns: repeat(2,1fr);
`

const FreelanceStyled = styled.div`
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items:center;
    color: ${({theme})=> theme === 'light' ? "#fff" : "#000"};
`

const LoaderWrapper = styled.div`
  display: flex;
  justify-content: center;
`

const TitleStyled1 = styled.h1`
    text-align:center;
    margin-bottom:70px; 
    font-size:30px;
    font-weight:700px;
    color: ${({theme})=> (theme === 'light') ? "#000" : "#fff"};
`


const TitleStyled2 = styled.h2`
    text-align:center;
    margin-bottom:90px;
    font-size:20px;
    font-weight:300px;
    color: ${({theme})=> (theme === 'light') ? colors.secondary : "#fff"};
`

function Freelances() {
    const {theme} = useContext(ThemeContext);
    const {data,isLoading,error} = useFetch('http://localhost:8000/freelances');
    const {freelancersList} = data;
    if(error){
        return <span>Oups quelques choses a mal tourne</span>
    }

    return (
        <FreelanceStyled theme={theme}>
            <div>
                <TitleStyled1 theme={theme}>Trouvez votre prestataire</TitleStyled1>
                <TitleStyled2 theme={theme}>Chez Shiny nous réunissons les meilleurs profils pour vous.</TitleStyled2>
            </div>

            {
                isLoading ? 
                    (<LoaderWrapper><Loader /></LoaderWrapper>) :
                    (<CardContainer>
                        {freelancersList.map((profile, index) => (
                            <Card
                                key={`${profile.name}-${index}`}
                                label={profile.job}
                                picture={profile.picture}
                                title={profile.name}
                            />
                        ))}
                    </CardContainer>)
            }
        </FreelanceStyled>
    )
}

export default Freelances;