import { Link } from 'react-router-dom'
import styled from 'styled-components'
import colors from '../../utils/styles/colors'
import logoDark from '../../assets/dark-logo.png'
import logoLight from '../../assets/light-logo.png'
import { ThemeContext } from '../../utils/contexte';
import { useContext } from 'react';


const StyledLink = styled(Link)`
    padding: 15px;
    color:${({theme})=>(theme === 'light') ? '#8186a0' : '#fff'};
    text-decoration: none;
    font-size: 18px;

    ${(props) => props.$isFullLink &&
        `color: white; border-radius: 30px; background-color: ${colors.primary};
         padding: 10px 30px;`}
    `

const StyledNav= styled.nav`
    display:flex;
    justify-content:space-between;
    align-items:center;
    padding: 20px 20px;

`

const StyledLogo = styled.div`
    display: flex;
`

function Header() {
    const {theme} = useContext(ThemeContext);
    return (
        <StyledNav>
            <StyledLogo>
                {
                    (theme === 'light') ? <img src={logoDark} alt="logo" /> : <img src={logoLight} alt="logo" />
                }
                 
            </StyledLogo>
            <div>
                <StyledLink theme={theme} to="/">Accueil</StyledLink>
                <StyledLink theme={theme} to="/freelances">Profils</StyledLink>
                <StyledLink theme={theme} to="/survey/1" $isFullLink >Faire le test</StyledLink>
            </div>
        </StyledNav>
    )
}

export default Header